<template>
  <span>
    TOTAL: {{ totalPrice }} EUR
  </span>
</template>

<script>
export default {
  name: "ToolBar",
  computed: {
    totalPrice() {
      let total = 0
      this.$store.getters.cart.map(x => {
        total += x.cost
      })
      return total
    }
  }
}
</script>

