<template>
  <div class="card">
    <img :src="product.photos[0]"/>
    <div class="card_content">
      <h4>{{ product.title }}</h4>
      <h4>Price: {{ product.price }}EUR</h4>
      <h4>Total: {{ product.cost }}EUR</h4>
      <div class="buttons">
        <button @click="remove">-</button>
        <h3>{{ product.amount }}</h3>
        <button @click="add">+</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProductInCart",
  props: {
    product: Object
  },
  methods: {
    add() {
      this.$store.commit('addToCart', this.product.id)
    },
    remove() {
      this.$store.commit('removeFromCart', this.product.id)
    }
  }
}
</script>

