<template>
  <div class="sec_1">
    <div class="card">
      <img :src="shoe.photos[0]" alt="">
      <div class="card_content">
        <h4>{{ shoe.title }}</h4>
        <h4>Left in stock: {{ shoe.stock }}</h4>
        <h4>Price: {{ shoe.price }} EUR</h4>
        <button @click="addToCart">Add to card</button>
      </div>
    </div>

  </div>

</template>

<script>
export default {
  name: "Shoes",
  computed: {
    shoe() {
      return this.$store.getters.singleShoe
    },
  },
  created() {
    this.$store.commit('filterShoes', this.$route.params.id)
  },
  methods: {
    addToCart() {
      this.$store.commit('addToCart', this.$route.params.id)
    }
  }

}
</script>

