  <div class="card" @click="navigate">
    <img :src="products.photos[0]" alt="">
    <div class="card_content">
      <h4>{{ products.title }}</h4>
      <h4>Left in stock: {{ products.stock }}</h4>
      <h4>Price: {{ products.price }} EUR</h4>
    </div>
  </div>
</template>

<script>

export default {
  name: "Card",
  props: {
    products: Object
  },
  methods: {
    navigate() {
      this.$router.push(`/product/${this.products.id}`)
    }
  }


}
</script>

